// swiper 对dom节点进行操作的,dom节点什么时候生产?
import Swiper from 'swiper'
import 'swiper/dist/css/swiper.css'

export default {
  name: 'swiper',
  props: {
    lists: {
      type: Array,
      required: true
    }
  },
  created() {
    console.log(this.lists)
  },
  mounted() {
    let mySwiper = new Swiper('.swiper-container', {
      autoplay: true,
      loop: true,
      effect: 'flip',
      pagination: {
        el: '.swiper-pagination',
        type: 'bullets'
      }
    })
  }
}



