import qs from 'qs'

let { index } = qs.parse(location.search.substr(1))

let configData = [
  {
    name: '有赞',
    icon: 'icon-home',
    href: 'index.html'
  },
  {
    name: '分类',
    icon: 'icon-category',
    href: 'category.html'
  },
  {
    name: '购物车',
    icon: 'icon-cart',
    href: 'cart.html'
  },
  {
    name: '我',
    icon: 'icon-user',
    href: 'member.html'
  }
]
export default {
  name: 'Foot',
  data () {
    return {
      configData: configData,
      curIndex: parseInt(index) || 0
    }
  },
  methods: {
    changeIndex (index, list) {
      window.location.replace(`${list.href}?index=${index}`)
    }
  }
}



